import { Request, Response } from 'express';
import syncMasterData from '@/services/master.service';
import { ResponsePattern } from '@/interfaces/response.interface';

class MasterController {
  public syncService = new syncMasterData();

  public syncMasterData = async (req: Request, res: Response): Promise<void> => {
    try {
      const stationId = BigInt(req.params.id);
      const response: ResponsePattern = await this.syncService.syncMasterData(stationId);
      res.status(response.statusCode).json(response);
    } catch (e) {
      console.log(e);
      res.status(400).json({ statusCode: 400, message: 'Bad Request' });
    }
  };
}

export default MasterController;
